import { FrameworkConfiguration, PLATFORM } from "aurelia-framework";
import { TopbarElements }                   from "./configuration.d";

export class PitayaTopbarConfiguration
{
	constructor( private _config: FrameworkConfiguration ){}

	public use( ...elements: TopbarElements[] )
	{
		const imports = elements.map( element => PLATFORM.moduleName( `./${element}` ) );

		this._config.globalResources( imports );
	}
}