import { FrameworkConfiguration, PLATFORM } from "aurelia-framework";
import { PitayaTopbarConfiguration }        from "./configuration";

export function configure( frameworkConfig: FrameworkConfiguration, callback?: ( config: PitayaTopbarConfiguration ) => void )
{
	if( callback instanceof Function )
	{
		frameworkConfig.globalResources( [
			PLATFORM.moduleName( "./pitaya-topbar" )
		] );

		const topbarConfig = new PitayaTopbarConfiguration( frameworkConfig );

		callback( topbarConfig );

		return;
	}

	frameworkConfig.globalResources( [
		PLATFORM.moduleName( "./pitaya-topbar" ),
		PLATFORM.moduleName( "./pitaya-topbar-row.html" ),
		PLATFORM.moduleName( "./pitaya-topbar-navigation.html" ),
		PLATFORM.moduleName( "./pitaya-topbar-actions.html" ),
		PLATFORM.moduleName( "./pitaya-topbar-action-icon.html" )
	] );
}

export * from "./pitaya-topbar";
export * from "./configuration";