import { MDCTopAppBar, MDCTopAppBarAdapter, MDCTopAppBarFoundation }  from "@material/top-app-bar";
import { autoinject, Container, LogManager, PLATFORM, viewResources } from "aurelia-framework";
import { bindable }                                                   from "aurelia-typed-observable-plugin";
import { PitayaBaseComponent }                                        from "pitaya-framework";
import { TopbarEvent }                                                from "./pitaya-topbar.d";

const logger = LogManager.getLogger( "Pitaya:Topbar" );

@autoinject
@viewResources( PLATFORM.moduleName( "./pitaya-topbar.css" ) )
export class PitayaTopbar implements PitayaBaseComponent
{
	@bindable public type: "short" | "dense" | "prominent";
	@bindable public onNavigation: Function;
	@bindable public onAttached: Function;

	private _mdcComponent: MDCTopAppBar;
	private _mdcFoundation: MDCTopAppBarFoundation;
	private _mdcAdapter: MDCTopAppBarAdapter;
	private _rootElement: HTMLElement;

	get container(): Container
	{
		return this._container;
	}

	constructor( private _container: Container ){}

	public setScrollTarget( target: HTMLElement )
	{
		if( !target )
		{
			logger.warn( "PitayaTopbar has either no parent {Container} or its parent {Container} has no HTMLElement defined on it. The scrollTarget will have to be set manually." );

			return;
		}

		this._mdcComponent.setScrollTarget( target );
	}

	public listen( eventName: TopbarEvent, handler: ( event?: CustomEvent ) => void, options?: AddEventListenerOptions )
	{
		this._mdcComponent.listen( eventName, handler, options );
	}

	public unlisten( eventName: TopbarEvent, handler: ( event?: CustomEvent ) => void, options?: AddEventListenerOptions )
	{
		this._mdcComponent.unlisten( eventName, handler, options );
	}

	public reinitialize(): Promise<void> | void
	{
		this.detached();
		this.attached();
	}

	private attached()
	{
		this._mdcComponent  = new MDCTopAppBar( this._rootElement );
		this._mdcFoundation = this._mdcComponent.getDefaultFoundation() as MDCTopAppBarFoundation;
		this._mdcAdapter    = this._mdcFoundation[ "adapter_" ];

		const parentElement = this._container.parent.get( Element ) as HTMLElement;

		this.setScrollTarget( parentElement );

		this.onAttached && this.onAttached( { component : this } );
		this.onNavigation && this.listen( "MDCTopAppBar:nav", ( event: CustomEvent ) =>
		{
			this.onNavigation( {
				event,
				component : this
			} );
		} );
	}

	private detached()
	{
		this._mdcComponent.destroy();
	}
}